/* ========================================
   üìß FORMULAIRE DE CONTACT EMAILJS - CSS
   ======================================== */

/* üéØ LAYOUT PRINCIPAL */
.contact-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 3rem;
  align-items: start;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* ========================================
   üìã CONTENEUR FORMULAIRE
   ======================================== */

.contact-form-container {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.contact-form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--primary-yellow), #ffd700, var(--primary-yellow));
  border-radius: 24px 24px 0 0;
}

.contact-form-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.15);
}

.contact-form-container h3 {
  color: var(--primary-yellow);
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  text-align: center;
  position: relative;
}

.contact-form-container h3::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, var(--primary-yellow), #ffd700);
  border-radius: 2px;
}

/* ========================================
   üé® FORMULAIRE
   ======================================== */

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.8rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  position: relative;
}

.form-group label {
  color: var(--text-color);
  font-weight: 600;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: color 0.3s ease;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 1.2rem 1rem;
  border: 2px solid rgba(255, 255, 255, 0.15);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-color);
  font-size: 1rem;
  font-family: inherit;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  position: relative;
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
  opacity: 1;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-yellow);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
  transform: translateY(-2px);
}

.form-group input:focus + label,
.form-group select:focus + label,
.form-group textarea:focus + label {
  color: var(--primary-yellow);
}

.form-group textarea {
  resize: vertical;
  min-height: 140px;
  max-height: 300px;
  line-height: 1.6;
}

/* ========================================
   ‚ö†Ô∏è GESTION DES ERREURS
   ======================================== */

.error-text {
  color: #ff6b6b;
  font-size: 0.85rem;
  font-weight: 500;
  margin-top: 0.25rem;
  display: flex;
  align-items: center;
  gap: 0.3rem;
  animation: errorShake 0.3s ease-out;
}

.form-group:has(.error-text) input,
.form-group:has(.error-text) select,
.form-group:has(.error-text) textarea {
  border-color: #ff6b6b;
  box-shadow: 0 0 10px rgba(255, 107, 107, 0.2);
}

/* ========================================
   üìä √âL√âMENTS UTILITAIRES
   ======================================== */

.char-count {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  text-align: right;
  margin-top: 0.3rem;
  transition: color 0.3s ease;
}

.char-count.warning {
  color: #ff9500;
  font-weight: 600;
}

.char-count.error {
  color: #ff6b6b;
  font-weight: 600;
}

.form-group:focus-within .char-count {
  color: var(--primary-yellow);
}

/* ========================================
   üöÄ BOUTONS
   ======================================== */

.btn-submit {
  background: linear-gradient(135deg, var(--primary-yellow), #ffd700);
  color: #1a1a2e;
  border: none;
  padding: 1.2rem 2.5rem;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  margin-top: 1.5rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  width: 100%;
}

.btn-submit::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.btn-submit:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
}

.btn-submit:hover:not(:disabled)::before {
  left: 100%;
}

.btn-submit:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.btn-reset {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 0.9rem 1.8rem;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 1.2rem;
  transition: all 0.3s ease;
  font-weight: 600;
  width: 100%;
}

.btn-reset:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: var(--primary-yellow);
  color: var(--primary-yellow);
  transform: translateY(-2px);
}

/* ========================================
   ‚úÖ MESSAGES D'√âTAT
   ======================================== */

.success-message {
  text-align: center;
  padding: 2.5rem;
  background: linear-gradient(135deg, #4caf50, #45a049);
  border-radius: 16px;
  color: white;
  box-shadow: 0 8px 32px rgba(76, 175, 80, 0.3);
  animation: fadeInScale 0.5s ease-out;
}

.success-message h4 {
  margin-bottom: 1.2rem;
  font-size: 1.4rem;
  font-weight: 700;
}

.success-message p {
  margin-bottom: 1.5rem;
  opacity: 0.9;
  line-height: 1.5;
}

.error-message {
  background: linear-gradient(135deg, #f44336, #e53935);
  color: white;
  padding: 1.2rem;
  border-radius: 10px;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 600;
  box-shadow: 0 4px 20px rgba(244, 67, 54, 0.3);
  animation: shakeError 0.5s ease-out;
}

/* ========================================
   üìù INFORMATIONS
   ======================================== */

.form-note {
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  line-height: 1.5;
  margin-top: 1.5rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.form-note strong {
  color: var(--primary-yellow);
  font-weight: 600;
}

/* ========================================
   üé≠ ANIMATIONS
   ======================================== */

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes shakeError {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

@keyframes errorShake {
  0%, 100% { transform: translateX(0); }
  20% { transform: translateX(-3px); }
  40% { transform: translateX(3px); }
  60% { transform: translateX(-2px); }
  80% { transform: translateX(2px); }
}

/* ========================================
   üì± RESPONSIVE DESIGN AVANC√â
   ======================================== */

/* Tr√®s grands √©crans */
@media (min-width: 1600px) {
  .contact-content {
    max-width: 1600px;
    gap: 4rem;
  }
  
  .contact-form-container {
    padding: 3rem;
  }
}

/* Tablettes portrait */
@media (max-width: 1024px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2.5rem;
    max-width: 800px;
  }
  
  .contact-form-container {
    padding: 2rem;
  }
}

/* Tablettes et mobiles large */
@media (max-width: 768px) {
  .contact-content {
    gap: 2rem;
    padding: 0 0.5rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .contact-form-container {
    padding: 1.8rem;
    border-radius: 20px;
  }
  
  .contact-form-container h3 {
    font-size: 1.4rem;
  }
  
  .btn-submit {
    padding: 1rem 2rem;
    font-size: 1rem;
    letter-spacing: 1px;
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: 1rem 0.9rem;
  }
}

/* Mobiles moyens */
@media (max-width: 640px) {
  .contact-form-container {
    padding: 1.5rem;
    margin: 0 0.5rem;
  }
  
  .contact-form {
    gap: 1.5rem;
  }
  
  .form-group label {
    font-size: 0.9rem;
  }
  
  .success-message,
  .error-message {
    padding: 1.5rem;
    margin: 0 0.5rem 1rem;
  }
}

/* Petits mobiles */
@media (max-width: 480px) {
  .contact-content {
    padding: 0;
  }
  
  .contact-form-container {
    padding: 1.2rem;
    border-radius: 16px;
    margin: 0;
  }
  
  .contact-form-container h3 {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
  }
  
  .contact-form {
    gap: 1.3rem;
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: 0.9rem 0.8rem;
    font-size: 0.95rem;
  }
  
  .form-group label {
    font-size: 0.85rem;
  }
  
  .btn-submit {
    padding: 1rem 1.5rem;
    font-size: 0.95rem;
    letter-spacing: 0.8px;
  }
  
  .form-note {
    font-size: 0.8rem;
    padding: 0.8rem;
  }
  
  .char-count {
    font-size: 0.75rem;
  }
  
  .error-text {
    font-size: 0.8rem;
  }
}

/* Tr√®s petits √©crans */
@media (max-width: 360px) {
  .contact-form-container {
    padding: 1rem;
    border-radius: 12px;
  }
  
  .contact-form-container h3 {
    font-size: 1.2rem;
  }
  
  .btn-submit {
    padding: 0.9rem 1.2rem;
    font-size: 0.9rem;
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: 0.8rem;
    font-size: 0.9rem;
  }
  
  .success-message h4 {
    font-size: 1.2rem;
  }
}

/* Orientation paysage pour mobiles */
@media (max-height: 500px) and (orientation: landscape) {
  .contact-form-container {
    padding: 1rem;
  }
  
  .contact-form-container h3 {
    margin-bottom: 1rem;
    font-size: 1.2rem;
  }
  
  .contact-form {
    gap: 1rem;
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: 0.8rem;
  }
  
  .form-group textarea {
    min-height: 100px;
  }
  
  .btn-submit {
    padding: 0.8rem 1.5rem;
    margin-top: 1rem;
  }
}